@import 'vendor/normalize.css';
@import '_common.scss';
@import '_grid.scss';
@import '_fonts.scss';
@import '_mixin.scss';
@import '_variables.scss';

.hero {
	padding-top: 41px;
	padding-bottom: 67px;
	&__grid-left {
		display: flex;
		align-items: flex-end;
		position: relative;
		background-image: url('../img/works1.jpg');
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		height: 100%;
		max-height: 630px;
		span {
			@include after;
			padding: 6px 14px;
			width: 123px;
			height: 31px;
			top: 15px;
			right: 15px;
		}
		div {
			padding-left: 60px;
			padding-bottom: 60px;
			p {
				margin-bottom: 3px;
				font-family: 'Roboto';
				font-weight: 400;
				line-height: 19px;
			}
			h2 {
				margin-bottom: 19px;
				width: 100%;
				max-width: 427px;
				font-weight: 700;
				font-size: 36px;
				line-height: 104%;
			}
		}
	}
}

.hero__right {
	img {
		object-fit: cover;
		width: 100%;
		height: 100%;
	}
	&-heading {
		display: flex;
		align-items: baseline;
		margin-bottom: 27px;
		margin-top: -5px;
		font-weight: 700;
		font-size: 24px;
		color: $darkBack;
	}
}

.hero__grid-works {
	height: 100%;
	max-height: 300px;
}

.hero__grid-works2, .hero__grid-works3 {
	height: 100%;
	max-height: 257px;
}

.hero-btn, .about-btn {
	@extend %btn;
}
