	.burger {
		position: relative;
		z-index: 2;
		display: block;
		width: 20px;
		height: 18px;
		color: $wight;
		transition: color .5s ease;
	}

	.burger:focus {
		outline: 2px solid #FF6E30;
		outline-offset: 5px;
	}

	.burger::before,
	.burger::after {
		content: "";
	}

	.burger span,
	.burger::before,
	.burger::after {
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 3px;
		background-color: currentColor;
		transform-origin: right;
		transition: transform .2s ease;
	}

	.burger::after {
		width: 50%;
	}

	.burger::before {
		transform: translateY(calc(-100% - 6px));
	}

	.burger span {
		top: calc(50% - 1px);
		transition: opacity .5s ease, transform .2s ease;
	}

	.burger::after {
		transform: translateY(calc(100% + 5px));
	}

	.burger:active {
		color: $btnColor;
	}

	.burger:active span {
		transform: scaleX(calc(25 / 30));
	}

	.burger:active::after {
		transform: scaleX(calc(18 / 30)) translateY(calc(100% + 5px));
	}

	.is-opened.burger span,
	.is-opened.burger::before,
	.is-opened.burger::after {
		transform-origin: center;
	}

	.is-opened.burger::before {
		top: 50%;
		transform: translateY(-50%) rotate(45deg);
	}

	.burger.is-opened span {
		opacity: 0;
		transform: translateX(-100%);
	}

	.is-opened.burger::after {
		top: 50%;
		width: 100%;
		transform: translateY(-50%) rotate(-45deg);
	}

	.burger.is-opened {
		background-color: #808080;
		outline: 5px solid #808080;
		outline-offset: -1px;
		border-radius: 100%;
	}

	.burger.is-opened:hover, .burger.is-opened:focus {
		background-color: $btnColor;
		outline: 5px solid $btnColor;
		outline-offset: -1px;
		border-radius: 100%;
	}

	.menu-wrap {
		position: fixed;
		left: 0;
		top: 69px;
		z-index: 1;
		display: none;
		width: 100%;
		height: 100vh;
		padding: 50px 30px 30px 22px;
		background-color: $darkBack;
		transform: translateX(-100%);
		a {
			margin-left: 4px;
		}
	}

	.menu-wrap.is-opened {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		transform: translateX(0);
		animation-name: menu-open;
		animation-duration: .3s;
		height: 90vh;
		span {
			margin-top: auto;
		}
	}

	.menu-wrap.is-closed {
		animation-name: menu-close;
		animation-duration: .3s;
	}

	@keyframes menu-open {
		from {
			transform: translateX(-100%);
		}
		to {
			transform: translateX(0);
		}
	}

	@keyframes menu-close {
		from {
			transform: translateX(0);
		}
		to {
